/* Add an element for use as a background where the other elemnt needs a seperate background element */
.bg {
  /* Requires position relative on parent element */
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  pointer-events: none;
  z-index: -1;
}
/* Add an overlay to an element. Add hover class to add hover state */
.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-color: #000;
  opacity: 0.6;
}
.overlay.hover {
  opacity: 0;
}
:hover > .overlay.hover {
  opacity: 0.6;
}
/* Add text that appers in the center of a parent element on hover. Parent needs position relative */
.hover-text {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  display: none;
}
.hover-text span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
:hover > .hover-text {
  opacity: 1;
  display: block;
}
