/*
CSS for the full screen carousel

JS: proto/js/demos/bg-carousel.js
HTML: proto/bg-carousel.php
*/
.bg-carousel-wrap {
  /* Outer element of carousel*/
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.bg-carousel-wrap .bg-carousel-static {
  /*
		This element stays static at the centre of the parent element
		when the transition has finished running the background image will swap to the new image
		*/
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.bg-carousel-wrap .bg-carousel-animate {
  /*
		This element is hidden most of the time
		*/
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
}
.bg-carousel-animate {
  /*
	The from-left and from from-right choose which side of the screen the elements come in from.
	*/
}
.bg-carousel-animate.from-left {
  left: -100%;
  display: block;
}
.bg-carousel-animate.from-right {
  left: 100%;
  display: block;
}
.bg-carousel-animate.animate {
  /*
		The animate class is given to the bg-carousel-animate element so that it only has a transition when it needs to
		*/
  display: block;
  transition: left 0.8s;
  /* IF YOU CHANGE THIS VALUE ALSO CHANGE IT IN THE JAVASCRIPT. ALTER THE setTimeout DURATION ON LINE 93 TO MATCH THIS VALUE */
  left: 0;
}
