}
.demo-background .bg {
  transition: background 1.2s;
  transition-delay: 0.2s;
}
/* demo of an element moving on button press. Pressing the button adds the transition class */
.demo-position {
  position: relative;
}
.demo-position .moving-element {
  transition: all 1.2s;
  transition-delay: 0;
}
.demo-position.transition .moving-element {
  top: calc(100% - 12px);
  left: calc(100% - 12px);
}
/* demo of a complex transform on button press. Pressing the button adds the transition class */
.demo-transform {
  position: relative;
}
.demo-transform .moving-element {
  transition: transform 1.2s;
  transition-delay: 0;
}
.demo-transform.transition .moving-element {
  transform: translateX(150px) translateY(80px) scale(8) rotate(720deg);
}
/*
Example of how to write animations:
	Firstly, create the keyframes
 */
@keyframes animation1 {
  0% {
    bottom: 0;
  }
  20% {
    bottom: 120px;
  }
  40% {
    bottom: 0;
  }
  60% {
    bottom: 80px;
  }
  80% {
    bottom: 0;
  }
  90% {
    bottom: 20px;
  }
  100% {
    bottom: 0;
  }
}
/*
Add the name of the animation, the duration, the iteration count, and the timing function.
If writing in plain css then this requires a lot of prefixing. Bootstrap auto-prefixes as it compiles however.
*/
.demo-animation-1.transition .moving-element {
  animation-name: animation1;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.demo-animation-1 .moving-element {
  top: auto;
  bottom: 0;
  left: calc(50% - 6px);
}
/* Another Example of how to write animations */
@keyframes animation2 {
  0% {
    background-image: linear-gradient(to top right, #999999, #222222);
  }
  20% {
    background-image: linear-gradient(to top left, #999999, #222222);
  }
  40% {
    background-image: linear-gradient(to bottom left, #999999, #222222);
  }
  60% {
    background-image: linear-gradient(to bottom right, #999999, #222222);
  }
  80% {
    background-image: linear-gradient(to top right, #999999, #222222);
  }
  100% {
    background-image: linear-gradient(to top right, #999999, #222222);
  }
}
.demo-animation-2.transition .bg {
  animation-name: animation2;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in;
}
