/* Size of the input and the margin between the inputs and the buttons*/
/* '+' and '-' buttons on number elements that can be clicked to increase or decrease the value */
.quantity-select {
  position: relative;
}
.quantity-select label,
.quantity-select .form-group {
  display: block;
}
.quantity-select label {
  /* Floats text onto the left and prevents it from streching over where the input with the buttons are */
  float: left;
  max-width: calc(100% - (3 * (38px + 8px)));
  line-height: 28px;
  margin-bottom: 0;
}
.quantity-select .form-group {
  /* Margin on the bottom of the element*/
  margin-bottom: 10px;
}
.quantity-select input {
  /* The number element */
  display: block;
  float: right;
  width: 38px;
  margin-right: 46px;
  padding: 3px 9px 3px;
  height: 28px;
  line-height: 1;
  text-align: center;
}
.quantity-select input[type=number]::-webkit-inner-spin-button,
.quantity-select input[type=number]::-webkit-outer-spin-button {
  /* Removes the default webkit styles */
  -webkit-appearance: none;
  margin: 0;
}
.quantity-select.last .form-group {
  margin-bottom: 22px;
}
.increment {
  /* Styles for the increment buttons. Also used for the additional field buttons */
  position: absolute;
  top: 0;
  width: 38px;
  background: #aaa;
  display: block;
  cursor: pointer;
  border-radius: 3px;
  font-size: 0;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  height: 28px;
}
.increment.down {
  right: 92px;
  /* Create the '-' sign with css: */
}
.increment.down:before {
  content: " ";
  position: absolute;
  top: 13px;
  left: 12px;
  width: 14px;
  height: 3px;
  background: #fff;
}
.increment.up {
  right: 0;
  /* Create the '+' sign with css: */
}
.increment.up:after {
  content: " ";
  position: absolute;
  top: 8px;
  left: 18px;
  width: 3px;
  height: 14px;
  background: #fff;
}
.increment.up:before {
  content: " ";
  position: absolute;
  top: 13px;
  left: 12px;
  width: 14px;
  height: 3px;
  background: #fff;
}
.increment:hover {
  /* Hover state of the increment buttons */
  background-color: #337ab7;
}
/* additional field button */
.additional-button {
  position: relative;
}
.additional-button span {
  position: relative;
  height: 28px;
  display: inline-block;
  margin-bottom: 10px;
}
.additional-button .increment {
  right: -46px;
  top: -1px;
}
